<template>

  <el-container class="el-container-wraper">
    <audio id="myAudio"
           
           @loadedmetadata="loadedmetadata"
           @canplay="canplay"
           @empted="empted"
           @play="play"
           @playing="playing"
           @ended="ended"
           @pause="pause"
           @seeking="seeking"
           @timeupdate="timeupdate"
           @volumechange="volumechange"
           @waiting="waiting"
           @abort="abort"
           @loadstart="loadstart">
      <source :src="song.url" type="audio/mpeg">
     
    </audio>
      <div class="body" v-if="!fullScreen" id="smallScreen" >
           <div :class="['aside-left', animationList[0]]">
             <!-- <img class="headIco" src="./assets/kawayi.png" />-->
             <span :class="['fa fa-music', animationList[2]]" style="position:absolute;color:#f0f0f0;font-size:small"></span>
             <span :class="['fa fa-music',animationList[1]]" style="position:absolute;color:#f9f8f6;font-size:small"></span>
         </div>
        <div class="aside-right">
         <div class="header-wraper">
         
           <p :class="[animationList[3]]" style="line-height: 1.5rem;font-size:small">{{song.name}} --{{song.singer}}</p>
        
            <div class="header">
              <span class="el-icon-arrow-left"></span>
              <span class="el-icon-mobile-phone" @click="fullScreenClick"></span>
               <span class="el-icon-close"></span>
           </div>
         </div>
          
           <div class="content">
               <div class="items">
                 <span class="fa fa-backward" @click="playBack"></span>
                  <span class="fa fa-play" @click="play" v-if="status==0"></span>
                  <span class="fa fa-pause" @click="pause" v-else></span> 
                  <span class="fa fa-forward" @click="playNext"></span>
                </div>
                <div class="progress-time">
                 <el-slider v-model="progressValue" 
                                @change="valueChage"
                                style="width: 65%"
                                :show-tooltip="true"></el-slider>
              
                    <p class="full-time">{{currentTime}}/{{duration}}</p>
                
                </div>
           </div>
           <div class="footer">
            <transition name="el-zoom-in-bottom">
             <div class="footer-value" v-show="leftValShow">
              <el-slider
                   v-model="valumnValue"
                   style="width:7vw; margin-right: 1vw"
                   
                   @change="volumnChange"
                   height="10vh">
               </el-slider>
                 
              </div>
               </transition>
            
              <div class="footer-content">
                
                  <span class="fa fa-volume-up" id="hide" :style="valShow"  @click="valumnShow"></span>
                
                  <span class="fa fa-circle-o-notch" v-if="playMethod==0" @click="playMethodChange"></span>
                  <span class="fa fa-random" v-else-if="playMethod==2" @click="playMethodChange"></span>
                  <span class="fa fa-circle-thin" v-else @click="playMethodChange"></span>
                  <span class="fa fa-list" @click="listShow=!listShow"></span>
               <!--  class="fa fa-arrows-alt" aria-hidden="true"-->
               <!-- class="fa fa-compress" -->
               <!-- el-icon-sort-up -->
               </div>
           </div>
        </div>
      </div>
      <transition name="el-zoom-in-center">
      <div class="list" v-show="listShow">
 
        <p v-for="(item,index) of source" class="item" :index="index" @click="selectPlay(index)">
          <span style="width: 40%;text-align: left;text-overflow:ellipsis;padding-left:8%">{{item.name}}</span>
          <span style="width: 40%;text-align: left">{{item.singer}}</span>
          <span class="fa fa-play-circle" style="width: 20%;text-align:left"></span>
        </p>
      </div>
      </transition>
 
      <div class="full-body" v-if="fullScreen" >
         <div class="full-header">
               <span class="el-icon-arrow-left"></span>
               <span class="el-icon-mobile-phone" @click="fullScreenClick"></span>
               <span class="el-icon-close"></span>
         </div>
         <div class="full-name">
            <p :class="[animationList[3],'full-song-name']" style="line-height: 1.5rem;">{{song.name}} --{{song.singer}}</p>
         </div>
         <div class="full-space">
             <div :class="['full-aside-left', animationList[0]]">
             <!-- <img class="headIco" src="./assets/kawayi.png" />-->
             <span :class="['fa fa-music', animationList[2]]" style="position:absolute;color:#f0f0f0;font-size:small"></span>
             <span :class="['fa fa-music',animationList[1]]" style="position:absolute;color:#f9f8f6;font-size:small"></span>
         </div>
         </div>
      
         <div class="full-progress">
                
                     <el-slider v-model="progressValue" 
                                @change="valueChage"
                                style="width: 80%"
                                :show-tooltip="true"></el-slider>
              
                    <p class="full-time">{{currentTime}}/{{duration}}</p>
        
         </div>
         <div class="full-button">
                <el-slider
                   v-model="valumnValue"
                   style="position: absolute;margin-top: -11vh;margin-left: -0.5vw"
                   vertical
                   @change="volumnChange"
                   height="10vh">
               </el-slider>
                <span class="fa fa-volume-up" id="hide"></span>
                
                  <span class="fa fa-circle-o-notch" v-if="playMethod==0" @click="playMethodChange"></span>
                  <span class="fa fa-random" v-else-if="playMethod==2" @click="playMethodChange"></span>
                  <span class="fa fa-circle-thin" v-else @click="playMethodChange"></span>
                  <span class="fa fa-list" @click="fullListShow=!fullListShow"></span>
         </div>
         <div class="full-controls" id="fscreen">
                  <span class="fa fa-backward" @click="playBack"></span>
                  <span class="fa fa-play" @click="play" v-if="status==0"></span>
                  <span class="fa fa-pause" @click="pause" v-else></span> 
                  <span class="fa fa-forward" @click="playNext"></span>
         </div>
      <transition name="el-zoom-in-top">
        <div class="full-list" v-show="fullListShow">
 
         <p v-for="(item,index) of source" class="full-items" :index="index" @click="selectPlay(index)">
            <span style="width: 40%;text-align: left;text-overflow:ellipsis;padding-left:8%">{{item.name}}</span>
            <span style="width: 40%;text-align: left">{{item.singer}}</span>
            <span class="fa fa-play-circle" style="width: 20%;text-align:left"></span>
        </p>
      </div>
     </transition>
     
   </div>
  </el-container>
</template>

<script>
    
    export default {
        name: "index",

        data(){
            return {
                
                progressValue: 0,
                valumnValue: 0,
                valShow: {
                      "visibility": "visible"
                },
                leftValShow: false,
                listShow: false,
                fullListShow: false,
                showName: true,
                staticUrl: "/static/",
                source: [
                    {
                        name: "山外小楼夜听雨",
                        url: "../static/山外小楼夜听雨.mp3",
                        singer: "任然"
                    },
                    {
                        name: "请先说你好",
                        url: "../static/请先说你好.mp3",
                        singer: "李瑨瑶"
                    },
                    {
                        name: "岁月神偷",
                        url: "../static/岁月神偷.mp3",
                        singer: "李瑨瑶"
                    }
                ],
                song: "",
                fullScreen: true,
                index: 0,
                duration: "--:--",
                currentTime: "00:00",
                playList: [],
                status: 0, //播放状态，0：暂停 1：播放
                playMethod: 0, //播放方式，0：顺序播放，1：单曲循环，2：随机
                animationList: ['turn-around','music','music2','font-dance'],
                snowflake: [],
                allScreen: {
                   
                }
            }            
        },
        methods: {
            valueChage(e){
               // this.progressValue = parseInt(document.getElementsByName("progressRange")[0].value,10);
                document.getElementById('myAudio').currentTime = this.progressValue/100 * document.getElementById('myAudio').duration;
                
            },
            volumnChange(e){
                 //this.valumnValue = parseInt(document.getElementsByName("progressRange")[1].value,10);
                 document.getElementById('myAudio').volume = this.valumnValue / 100;
                 console.log(document.getElementById('myAudio').volume)
            },
            valumnShow(e){
               // this.valShow = {"visibility": "hidden"};
                this.leftValShow = true;
            },
            mouseLeave(e){
                this.leftValShow = false;
                setTimeout(()=>{
                       this.valShow = {"visibility": "visible"};
                },300)
                
            },
            init(){
                
                this.song = this.source[this.index];
                this.stopAnimation()
                 for(let i=0;i<this.source.length;i++){
                    this.playList.push(i)
                }
              //  this.index = this.index;
            },
            /**
 @loadedmetadata="loadedmetadata"
           @canplay="canplay"
           @empted="empted"
           @play="play"
           @playing="playing"
           @ended="ended"
           @pause="pause"
           @seeking="seeking"
           @timeupdate="timeupdate"
           @volumechange="volumechange"
           @waiting="waiting"
           @abort="abort"
           @loadstart="loadstart">

            **/
           loadedmetadata(){
               console.log("音频的元数据已加载");
                this.valumnValue = document.getElementById('myAudio').volume * 100;
              //console.log(document.getElementById('myAudio').duration)
               let seek = document.getElementById('myAudio').duration;
               let seekMin = Math.floor(seek/60);
         
               let str = String(seek);
               let index = str.indexOf(".");
               let seekSecond = str.slice(index+1,index+2);
               this.duration = `${seekMin>9?seekMin:'0'+seekMin}:${seekSecond>9?seekSecond:'0'+seekSecond}`;
           },
           canplay(){
               console.log("音频有效");

           },
           minSecond(value){
                let secondTime = parseInt(value);// 秒
                let minuteTime = 0;// 分        
                if(secondTime >= 60) {//如果秒数大于60，将秒数转换成整数
                 //获取分钟，除以60取整数，得到整数分钟
                      minuteTime = parseInt(secondTime / 60);
                //获取秒数，秒数取佘，得到整数秒数
                      secondTime = parseInt(secondTime % 60);
                   }
                     let result = `00:${parseInt(secondTime)>9?parseInt(secondTime):'0'+parseInt(secondTime)}`;
                      if(minuteTime > 0) {         
                       result = `${parseInt(minuteTime)>9?parseInt(minuteTime):'0'+parseInt(minuteTime)}:${parseInt(secondTime)>9?parseInt(secondTime):'0'+parseInt(secondTime)}`
                  }   
                       return result;

           },
           empted(){
               throw "文件加载出错"
           },
           play(){
               //播放的动作
               document.getElementById('myAudio').play();
               this.startAnimation()
               this.status = 1;
           },
           message(content="这里应有提示"){
                  const h = this.$createElement;
                     this.$message({
                         message: h('p', null, [
                           
                             h('span', { style: 'color: teal' }, content)
                 ])
              });
           },
           notice(title="emmmm...",content="这是一则提醒消息"){
                const h = this.$createElement;

                   this.$notify({
                        title: title,
                        message: h('span', { style: 'color: teal'}, content)
               });
           },
           playing(e){
               console.log(document.getElementById('myAudio').currentTime)
           },
           ended(){
               //结束播放的事件
               this.playNext();
               this.notice('即将播放:',`${this.source[this.index].name} --${this.source[this.index].singer}`)
           },
           pause(){
               //暂停播放的动作
               document.getElementById('myAudio').pause();
               this.status = 0;
               this.stopAnimation()
           },
           stopAnimation(){
               this.animationList.fill(null);
               console.log(this.animationList)
           },
           startAnimation(){
               this.animationList = ['turn-around','music','music2','font-dance']
              // console.log(this.animationList)
           },
           playMethodChange(e){
               this.playMethod>=2? this.playMethod=0:this.playMethod+=1;

               if(this.playMethod==2){
                   //打乱播放列表
                 const r = Array.apply(null, { length: this.source.length })
                                         .map((n, i) => i)
                                           .map((n, i, all) => {
                                             const j = i + Math.floor(Math.random() * (all.length - i));
                                             const v = all[j];
                                             all[j] = n;
                                           return v;
                                     }).slice(0, this.playList.length).map(i => {
                                         return this.playList[i];
                                });
                    this.playList = [...r];
                  //console.log(this.playList)
               }else if(this.playMethod==0){
                   for(let n=0;n<this.source.length;n++){
                        this.playList.push(n)
                   }
               }
                  let index;
                    if(this.playMethod==0){
                      index="循环播放"
                  }else if(this.playMethod==1){
                      index="单曲循环"
                  }else {
                      index="随机播放"
                  }
                  this.message(index)
           },
           playNext(){
               this.index>=this.playList.length-1? this.index=0:this.index+=1;
               this.song = this.source[this.playList[this.index]];
               this.reloadPlay()
           },
           playBack(){
               this.index<=0? this.index=this.playList.length-1:this.index-=1;
               this.song = this.source[this.playList[this.index]];
               this.reloadPlay()
            //    document.getElementById('myAudio').play()
           },
           seeking(){
               //指示定位触发
               
           },
           reloadPlay(){
               document.getElementById('myAudio').load()
               document.getElementById('myAudio').play()
           },
           selectPlay(index){
                this.index = index;
                this.song = this.source[this.playList[index]];
                this.reloadPlay()
               // document.getElementById('myAudio').play()

           },
           timeupdate(){
               //播放位置改变时触发
              this.currentTime = this.minSecond(document.getElementById('myAudio').currentTime);
              try{
                  if(document.getElementById('myAudio').currentTime&&document.getElementById('myAudio').duration){
                       this.progressValue = Number((document.getElementById('myAudio').currentTime / document.getElementById('myAudio').duration *100).toFixed(0));
                  }
                 
              }catch(event){

              }
              
           },
           valumeShowVer(){},
           volumechange(){
               //改变音量触发

           },
           waiting(){
               //缓冲触发
           },
           abort(){
               //在音频或视频终止加载时触发
           },
           loadstart(){
               //加载时触发
              
           },
           fullScreenClick(){
               this.fullScreen = !this.fullScreen
           },
           createSnow(){
            let index = this.fullScreen? 16:3;
           // let ID = this.fullScreen? 'smallScreen':'fscreen';
            for(let i=0;i<index;i++){
                  let fontSizeInt = `font-size:${Math.random()*2}rem`;
                  let fontSizeEnd = `font-size:${Math.random()*2}rem`;
                  let fallDownInt = `top:${Math.random()*10}%`;
                  let fallDownEnd = `top:${Math.random()*20+90}%`;
                  let offsetX = `left:${Math.random()*100}%`;
                  let offsetXEnd = `left:${Math.random()*100}%`;
                  let rotateInt = `transform: rotate(${Math.random()*60})`;
                  let rotateX = `transform: rotateX(${Math.random()*360+40}deg)`;
                  let rotateY = `transform: rotateY(${Math.random()*360+40}deg)`;
                  let rotateZ = `transform: rotateZ(${Math.random()*180+40}deg)`;
                  let rotatex = `transform: rotateX(${Math.random()*100+40}deg)`;
                  let rotatey = `transform: rotateY(${Math.random()*100+40}deg)`;
                  let rotatez = `transform: rotateZ(${Math.random()*100+40}deg)`;
                  let scale = `transform: scale(${Math.random()*3+1})`;
                  let timer = `${Math.random()*10+2}s`;
                  let delay = `${Math.random()*5}s`;
                  try{
                        let style = document.getElementById('myStyle').sheet;
                          console.log(style)
                       　 style.insertRule( `@keyframes myMove${i}{0% {${fontSizeInt};${fallDownInt};${offsetX};${rotateInt}} 50%{${scale};${rotatex};${rotatey};${rotatez}} 100%{ ${fontSizeEnd};${fallDownEnd};${offsetXEnd};${rotateX};${rotateY};${rotateZ}}}`)
                   
                  //  style.styleSheet.cssText = "body{background-color:red}";//针对IE
                          style.insertRule(`.snowflake${i}{position:absolute;animation:myMove${i} ${timer} linear ${delay} infinite;color: #fff;}`);
          
                  }catch(e){
                     
                  }
              
                let ele = document.createElement('span');
                         ele.classList.add(`snowflake${i}`,'fa','fa-snowflake-o');
                         document.getElementById('fscreen').appendChild(ele);
             }
           }

        },
        watch:{
           fullScreen(newData,old){
               if(newData){
                    
               }
           }
        },
        created(){
           this.init()
        },
        mounted(){
         setTimeout(()=>{

            this.createSnow()
         },100)  
              
        }
   
        

    }
</script>

  
  <style scoped>

     .el-container-wraper {
           top: 0;
           right: 0;
           bottom: 0;
           left: 0;
           background: #f0f0f0;
           position: fixed;
           display: flex;
           flex-direction: column;
           justify-content: center;
           align-items: center;
     }
      span {
          cursor: pointer;
      }
     
     .body {
         width: 30vw;
         height: 23vh;
         perspective: 200px;
         display: flex;
         flex-direction: row;
         background: linear-gradient(to right, #83a4d4, #b6fbff);  
       
         justify-content: space-around;
         border-radius: 12px;
         overflow: hidden;
     }
   
     .body>.aside-left {
          
          height: 10vw;
          margin: 0 1rem;
