     }
   
     .headIco {
         width: auto;
         height: auto;
         max-width: 100%;
         max-height: 100%;
         
     }
   
     .aisde-right {     
         height: 100%;      
         text-align: center;
     }
     
     .header-wraper,.footer {
          height: 25%;
          perspective:500px;
          display: flex;
          flex-direction: row;
          justify-content: flex-end;     
          align-items: center;
          width: 21vw;
     }
     .header-wraper {
         justify-content: space-between;
     }
     .footer {
         width: 100%;
        
         padding-right: 0.35rem;
         
     }
      .footer-content {
         width: 33%;
         font-size: 1.22rem;
         display: flex;
         flex-direction: row;
         justify-content: space-between;
         padding-right: 1.35rem;
      }
     .header {
         
         width: 40%;
         display: flex;
         flex-direction: row;
         justify-content: space-around;
         align-self: flex-start;
     }
     .header span {
         font-size: 1.6rem;
         padding: 0.3rem;
     }
 
    .content {
            height: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;     
            align-items: center;
          
     }
     .items {
         display: flex;
         flex-direction: row;
