         align-items: flex-end;
         width: 55%;
     }
    .progress-time {
           display: flex;
           flex-direction: row;
           align-items: flex-start;
           justify-content: space-around;
           
           width: 70%;
     }
    .progress-range-volumn {
         z-index: 3500;
         width: 80%;
         margin-top: -1.2rem;
        
         opacity: 0;
    }
    .list {
        max-height: 14vh;
       
        overflow-y: scroll;
        width: 30vw;  
        background: linear-gradient(to right, #83a4d4, #b6fbff); 
        margin-top: -1vh;
        border-radius: 0 0 12px 12px;
    }
    .list::-webkit-scrollbar { 
        width: 0 !important 
    }
    .list { 
        -ms-overflow-style: none; 
