        color: rgba(233,245,235,0.8);
      
    }
      .full-body {
         width: 100vw;
         height: 100vh;
         perspective: 500px;
         display: flex;
         flex-direction: column;
         background: linear-gradient(to right, #83a4d4, #b6fbff);       
         justify-content: space-around;
         border-radius: 12px;
         overflow: hidden;
         align-items: center;
     }
    .full-aside-right {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #fff;
        width: 60%;
    }
    .full-header {
        display: flex;
