    }
    .full-song-name {
        text-align: center;
    }
    .full-space {
        width: 100%;
        height: 38vh;
        display: flex;
        justify-content: center;
    }
      .full-aside-left {
          
          height: 100%; 
          width: 38vh;
          background: url('../static/Pio.png') no-repeat center;
          background-size: 70%;
     }
 
    .full-progress {
        width: 70%;
        height: 8vh;
        display: flex;
        flex-direction: row;
        align-items: center;

    }
    .full-progress-range {
        width: 100%;
