        opacity: 0;
        z-index: 320;
    }
    .full-time {
       font-size: 0.8rem;
       width: 20%;
       line-height: 0.8rem;
    }
    .full-button {
        margin-right: 8%;
        height: 15vh;
        display: flex;
        flex-direction: column;
      
        justify-content: space-between;
        font-size: 1.3rem;
       align-self: flex-end;
    }
    .full-controls {
        width: 60%;
        height: 7vh;
        display: flex;
        flex-direction: row;
