    }
    .full-list {
        width: 50%;
        display: flex;
        flex-direction: column;
        border-top: 1px solid #f0f0f0;
    }
    .full-items {
        display: flex;
        padding: 0.5rem 0;
        flex-direction: row;
        justify-content: space-around;
      
    }
    .full-list>.full-items:nth-child(n+2) {
        margin-top: -1rem;
    }
    .full-items:hover {
        background: #83a4d4;
        border-radius: 15px;
        color: #ffffff
    }
    .heartbeat {
        position: absolute;
        color: #f0f0f0;
        animation: heartbeat 12s linear 1s infinite;
    }
    .turn-around {
        animation: turnAround 6s ease-out 2s infinite;
    }
    .music {
        animation: musicDance 2s linear 1s infinite;
    }
    .music2 {
        animation: musicDance2 3s linear 1s infinite;
    }
    .font-dance {
        animation: fontDance 10s ease 1ms infinite;
        
    }
    @keyframes turnAround {
        0% {background: url('../static/Pio.png') no-repeat center;background-size: 60%;transform:rotateY(0deg)}
        25% {background: url('../static/Pio2.png') no-repeat center;background-size: 60%;transform:rotateY(-4deg)}
        35% {background: url('../static/Pio3.png') no-repeat center;background-size: 60%;transform:rotateY(5deg)}
        60% {background: url('../static/Pio4.png') no-repeat center;background-size: 60%;transform:rotateY(0)}
        80% {background: url('../static/Pio2.png') no-repeat center;background-size: 60%;transform:rotateY(-4deg)}
        100% {background: url('../static/Pio5.png') no-repeat center;background-size: 60%;transform:rotateX(4deg)}
    }
    @keyframes musicDance {
        0% { top:30%;left:45%}
        50% {top: 15%;left: 35%;color:#b5f0f2}
        100% {top:0;left: 33%;transform:rotateZ(180deg);transform:rotateY(360deg)}
    }
      @keyframes musicDance2 {
        0% { top:35%;left:28%}
        60% {top: 21%;left: 32%;color:#fbf}
        100% {top:0;left: 20%;transform:rotateX(360deg)}
    }
    @keyframes fontDance {
        0% {color: rbga(244,253,255,0.8)}
        20% {transform:rotateX(-10deg)}
        40% {transform:rotateX(10deg)}
        60% {color:#daf}
        70% {color:#fbc}
        85% {color:#fff}
        100% {color: rgba(245,235,255,1)}
    }
    @keyframes heartbeat {
        0% {color: rgba(#fff);top:85%;left: 0%;font-size: 2.6rem}
        20% {font-size: smaller;transform: rotateX(360deg)}
        50% {font-size: 2.8rem;transform: rotateX(260deg)}
        70% {color: rgba(255,245,255,0.5);top:89%;transform: rotateX(80deg);left:100%;font-size: 2rem;transform: rotateZ(20deg);transform: rotateX(240deg)}
        
    }
     @media screen and (max-width: 768px){
        .body {
             display: flex;
             width: 90vw;
       }
       .list {
           width: 90vw;
       }
     .body>.aside-left {
          width: 27vw;
          height: 100%;
          
     }
       .aisde-right {
         width: 63vw;
         height: 100%;
        
         text-align: center;
        
     }
 
       .header-wraper,.footer {
          height: 25%;
         
          display: flex;
          flex-direction: row;
          justify-content: flex-end;     
          align-items: center;
          width: 63vw;
          
     }
     .progress-range {
         width: 75%;
     }
      .progress-range-volumn {
       
         width: 80%;
        
      }
    
     }

     

  </style>


